<template>
  <div id="app">
    <ul class="nav">
      <li><a v-link="{ path: '/home' }">musics</a></li>
      <li><a v-link="{ path: '/router2' }">router2</a></li>
      <li><a v-link="{ path: '/router3' }">router3</a></li>
      <li><a v-link="{ path: '/router4' }">router4</a></li>
    </ul> 
    <router-view class="ui-view" keep-alive :transition="effect" transition-mode="out-in"></router-view>
  </div>
</template>
<script>
export default {
  data () {
   return {
      effect: 'fade' //路由模板动画参数
      
   }
  }
}
</script>

<style lang="scss">
  *{
    padding:0;
    margin:0;
  }
  .ui-view {
    width: 300px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
  }
  .fade-enter{
    opacity: 1;
    transform: translate3d(100%, 0, 0);
  }
  .fade-leave {
    opacity: 0; 
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);

  }
  .nav{
    margin:10px 0;
    overflow:hidden;
    position:fixed;
    width: 300px;
    top: 50px;
    li{
      line-height: 30px;
      background:#42b983;
      list-style:none;
      width: 25%;
      float:left;
      a{
        display: block;
        color: #fff !important;
        border-right:1px solid #fff;
      }
      &:last-of-type{
        a{
          border-right:none;
        }
      }
    }
  }
  a.nav-active{
    background:red;
  }
</style>



